.header {
  position: fixed;
  width: 100%;
  height: 65px;
  text-align: center;
  color: white;
  top: 0;
  left: 0;
  background-color: #344e41;
}

.left {
  position: absolute;
  width: 49%;
  left: 100px;
  text-align: left;
}

.right {
  position: absolute;
  width: 49%;
  right: 0;
}

.nav-bar {
  width: 100%;
  height: 65px;
  display: flex;
}

.nav-bar button {
  width: 100%;
  height: 100%;
  border: none;
  background-color: #588157;
  color: white;
}

.nav-bar button:hover {
  background-color: #3a5a40;
  cursor: pointer!important;
}
