.header {
  position: fixed;
  width: 100%;
  height: 65px;
  text-align: center;
  color: white;
  top: 0;
  left: 0;
  background-color: #344e41;
}

.left {
  position: absolute;
  width: 49%;
  left: 100px;
  text-align: left;
}

.right {
  position: absolute;
  width: 49%;
  right: 0;
}

.nav-bar {
  width: 100%;
  height: 65px;
  display: flex;
}

.nav-bar button {
  width: 100%;
  height: 100%;
  border: none;
  background-color: #588157;
  color: white;
}

.nav-bar button:hover {
  background-color: #3a5a40;
  cursor: pointer!important;
}

.footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  text-align: center;
  background-color: #dad7cd;
  font-size: 11px;
}

.home-container {
  width: 100%;
  margin-bottom: 10%;
}

.home-container h3 {
  margin-left: 35px;
}

#main-image {
  display: block;
  -webkit-transform: rotate(270deg);
          transform: rotate(270deg);
  width: 360px;
  height: auto;
  margin-right: auto;
  margin-left: auto;
  max-width: 390px;
  transition: width 1s, height 1s;
}

#main-image:hover {
  width: 390px;
}

.skillContainer {
  margin: 15px auto;
  width: 70%;
}

.mainbar {
  margin-left: auto;
  margin-right: auto;
  height: 15px;
  width: 100%;
  background: #dad7cd;
  border-radius: 20px;
  border: none;
}

.progress {
  width: 50%;
  height: 15px;
  background: #588157;
  border-radius: 20px;
  border: none;
}

.contactContainer{
  margin-top: 25px;
}

.socials {
  display: flex;
  flex-direction: row;
  justify-content: center;
  grid-gap: 150px;
  gap: 150px;
  height: 86px;
}

.socials a {
  width: 72px;
  height: 72px;
  max-width: 86px;
  max-height: 86px;
}

.social {
  width: 72px;
  height: 72px;
  max-width: 86px;
  max-height: 86px;
  transition: width 0.8s, height 0.8s;
}

.social:hover {
  width: 86px;
  height: 86px;
  cursor: pointer!important;
}

.formContainer {
  position: relative;
  display: block;
  width: 700px;
	padding: 20px 0px 25px 0px;
	margin-left: auto;
	margin-right: auto;
}

.contactForm {
  display: block;
  width: 700px;
  margin-left: auto;
  margin-right: auto;
}

.contactForm input, textarea {
  display: block;
	padding: 7px 12px;
	margin: 1.5% 0px 1% 0px;
  width: 675px !important;
}

#submit {
  display: block;
	padding: 3%;
	width: 700px;
  margin-left: auto;
  margin-right: auto;
	border-radius: 2px;
	border-width: 1px;
}

#content {
  padding-bottom: 5.5%;
  max-width: 100%;
  overflow-y: scroll;
}

.player {
	position: relative;
	background-color: green;
	width: 20px;
	height: 20px;
}

.apple {
	position: relative;
	height: 20px;
	width: 20px;
	background-color: red;
}

#game-container {
	width: 300px;
	height: 300px;
	margin-left: auto;
	margin-right: auto;
}

.minigame-container {
	width: 300px;
	height: 300px;
	margin-top: 30px;
	margin-left: auto;
	margin-right: auto;
	background-color: #e7ecf6;
}

#score {
	text-align: center;
}

.menu {
	position: relative;
	width: 300px;
	margin-left: auto;
	margin-right: auto;
	top: 20px;
	text-align: center;
}

.gameOver {
	text-align: center;
}

body {
  font-family: 'Ubuntu', sans-serif;
  background-color: #dad7cd;
  overflow-y: scroll;
}

input, textarea, button {
  font-family: 'Ubuntu', sans-serif;
}

p {
  font-size: 19px;
}

h1 {
  color: #344e41;
}

button {
	width: 175px;
	padding: 15px 45px;
	margin-top: 10px;
	border-radius: 5px;
	border: none;
	background-color: #588157;
	color: white;
}

button:hover {
	background-color: #3a5a40;
  cursor: pointer!important;
  color: white;
}

.game-content {
  position: relative;
  padding: 5px;
  width: 65%;
  height: 100%;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 4%;
}

.content {
  position: relative;
  padding: 5px;
  width: 65%;
  height: 100%;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 4%;
}

.content img {
  width: 98%;
}

#fun {
  float: right;
  margin-right: 15px;
  width: 195px;
  height: 46px;
  transition: width 0.5s, height 0.5s;
}

#fun:hover {
  width: 205px;
  height: 56px;
}

