body {
  font-family: 'Ubuntu', sans-serif;
  background-color: #dad7cd;
  overflow-y: scroll;
}

input, textarea, button {
  font-family: 'Ubuntu', sans-serif;
}

p {
  font-size: 19px;
}

h1 {
  color: #344e41;
}

button {
	width: 175px;
	padding: 15px 45px;
	margin-top: 10px;
	border-radius: 5px;
	border: none;
	background-color: #588157;
	color: white;
}

button:hover {
	background-color: #3a5a40;
  cursor: pointer!important;
  color: white;
}

.game-content {
  position: relative;
  padding: 5px;
  width: 65%;
  height: 100%;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 4%;
}

.content {
  position: relative;
  padding: 5px;
  width: 65%;
  height: 100%;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 4%;
}

.content img {
  width: 98%;
}

#fun {
  float: right;
  margin-right: 15px;
  width: 195px;
  height: 46px;
  transition: width 0.5s, height 0.5s;
}

#fun:hover {
  width: 205px;
  height: 56px;
}
