.contactContainer{
  margin-top: 25px;
}

.socials {
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 150px;
  height: 86px;
}

.socials a {
  width: 72px;
  height: 72px;
  max-width: 86px;
  max-height: 86px;
}

.social {
  width: 72px;
  height: 72px;
  max-width: 86px;
  max-height: 86px;
  transition: width 0.8s, height 0.8s;
}

.social:hover {
  width: 86px;
  height: 86px;
  cursor: pointer!important;
}

.formContainer {
  position: relative;
  display: block;
  width: 700px;
	padding: 20px 0px 25px 0px;
	margin-left: auto;
	margin-right: auto;
}

.contactForm {
  display: block;
  width: 700px;
  margin-left: auto;
  margin-right: auto;
}

.contactForm input, textarea {
  display: block;
	padding: 7px 12px;
	margin: 1.5% 0px 1% 0px;
  width: 675px !important;
}

#submit {
  display: block;
	padding: 3%;
	width: 700px;
  margin-left: auto;
  margin-right: auto;
	border-radius: 2px;
	border-width: 1px;
}

#content {
  padding-bottom: 5.5%;
  max-width: 100%;
  overflow-y: scroll;
}
