.home-container {
  width: 100%;
  margin-bottom: 10%;
}

.home-container h3 {
  margin-left: 35px;
}

#main-image {
  display: block;
  transform: rotate(270deg);
  width: 360px;
  height: auto;
  margin-right: auto;
  margin-left: auto;
  max-width: 390px;
  transition: width 1s, height 1s;
}

#main-image:hover {
  width: 390px;
}
